.App {
  font-family: sans-serif;
  text-align: center;
}

.custom-btn {
  background-color: #6d963e !important;
  border: 0px solid !important;
  color: #ffffff;
  box-shadow: 0 0 2px #000000 !important;
}

.custom-btn:hover {
  background-color: #6d963e !important;
  border: 0px solid !important;
  color: #000000 !important;
  box-shadow: 0 0 2px #000000 !important;
}

.custom-btn-video {
  color: #ffffff !important;
  background-color: #1b2e5f !important;
  border-radius: 100px;
  width: 200px;
  padding-top: 5%;
}

.custom-btn-video:hover {
  background-color: rgb(56, 77, 129) !important;
}

h1 {
  font-size: min(max(30px, 5vw), 60px);
}

h2 {
  font-size: min(max(20px, 4vw), 30px);
}

p {
  font-size: min(max(20px, 3vw), 30px);
}

.modal-90w {
  width: 90%;
  max-width: none;
}

.modal-contents-80w {
  width: 80%;
  max-width: none;
}
